import { WixDataItem, WixDataBulkResult, WixDataPatchOptions } from './types'

export interface FieldModification {
  fieldPath: string
  action:
    | 'SET_FIELD'
    | 'REMOVE_FIELD'
    | 'INCREMENT_FIELD'
    | 'APPEND_TO_ARRAY'
    | 'REMOVE_FROM_ARRAY'
  actionOptions?: any
}

/**
 * @builder
 */
export interface WixDataBulkPatch {
  /**
   * Refines a patch operation to increment the specified field value by the specified parameter.
   * @public
   * @documentationMaturity preview
   * @param field - Field whose value is incremented. Nested fields can be specified using dot notation. For example: `address.street`.
   * @requiredField field
   * @param by - Value by which to increment the specified field. To decrement, specify a negative value.
   * @requiredField by
   * @returns Updated patch object.
   */
  incrementField(field: string, by: number): WixDataBulkPatch

  /**
   * Refines a patch operation to set the specified field to the specified value.
   * @public
   * @documentationMaturity preview
   * @param field - Field whose value is set. Nested fields can be specified using dot notation. For example: `address.street`.
   * @requiredField field
   * @param value - Value to set for the specified field.
   * @requiredField value
   * @returns Updated patch object.
   */
  setField(field: string, value: any): WixDataBulkPatch

  /**
   * Refines a patch operation to append the specified value to the specified array field.
   * @public
   * @documentationMaturity preview
   * @param field - Array field to append the specified value to. Nested fields can be specified using dot notation. For example: `address.street`.
   * @requiredField field
   * @param value - Value to append to the specified array field.
   * @requiredField value
   * @returns Updated patch object.
   */
  appendToArray(field: string, value: any): WixDataBulkPatch

  /**
   * Refines a patch operation to remove the first instance of the specified value from the specified array field.
   * @public
   * @documentationMaturity preview
   * @param field - Array field to remove the specified value from. Nested fields can be specified using dot notation. For example: `address.street`.
   * @requiredField field
   * @param value - Value whose first instance is removed from the specified array field.
   * @requiredField value
   * @returns Updated patch object.
   */
  removeFromArray(field: string, value: any): WixDataBulkPatch

  /**
   * Refines a patch operation to clear the specified field.
   * @public
   * @documentationMaturity preview
   * @param field - Field whose value is cleared. Nested fields can be specified using dot notation. For example: `address.street`.
   * @requiredField field
   * @returns Updated patch object.
   */
  removeField(field: string): WixDataBulkPatch

  /**
   * Runs the bulk patch operation and returns a Promise that resolves to information about the results.
   *
   * @public
   * @documentationMaturity preview
   * @returns Bulk operation results.
   */
  run(options?: WixDataPatchOptions): Promise<WixDataBulkResult>
}

/**
 * @builder
 */
export interface WixDataPatch {
  /**
   * Refines a patch operation to increment the specified field value by the specified parameter.
   * @public
   * @documentationMaturity preview
   * @param field - Field whose value is incremented. Nested fields can be specified using dot notation. For example: `address.street`.
   * @requiredField field
   * @param by - Value by which to increment the specified field. To decrement, specify a negative value.
   * @requiredField by
   * @returns Updated patch object.
   */
  incrementField(field: string, by: number): WixDataPatch

  /**
   * Refines a patch operation to set the specified field to the specified value.
   * @public
   * @documentationMaturity preview
   * @param field - Field whose value is set. Nested fields can be specified using dot notation. For example: `address.street`.
   * @requiredField field
   * @param value - Value to set for the specified field.
   * @requiredField value
   * @returns Updated patch object.
   */
  setField(field: string, value: any): WixDataPatch

  /**
   * Refines a patch operation to append the specified value to the specified array field.
   * @public
   * @documentationMaturity preview
   * @param field - Array field to append the specified value to. Nested fields can be specified using dot notation. For example: `address.street`.
   * @requiredField field
   * @param value - Value to append to the specified array field.
   * @requiredField value
   * @returns Updated patch object.
   */
  appendToArray(field: string, value: any): WixDataPatch

  /**
   * Refines a patch operation to remove the first instance of the specified value from the specified array field.
   * @public
   * @documentationMaturity preview
   * @param field - Array field to remove the specified value from. Nested fields can be specified using dot notation. For example: `address.street`.
   * @requiredField field
   * @param value - Value whose first instance is removed from the specified array field.
   * @requiredField value
   * @returns Updated patch object.
   */
  removeFromArray(field: string, value: any): WixDataPatch

  /**
   * Refines a patch operation to clear the specified field.
   * @public
   * @documentationMaturity preview
   * @param field - Field whose value is cleared. Nested fields can be specified using dot notation. For example: `address.street`.
   * @requiredField field
   * @returns Updated patch object.
   */
  removeField(field: string): WixDataPatch

  /**
   * Runs the patch operation and returns a Promise that resolves to the patched item and operation metadata.
   *
   * @public
   * @documentationMaturity preview
   * @returns Modified data item.
   */

  run(options?: WixDataPatchOptions): Promise<WixDataItem | null>
}
